import pubfooter from '@/components/pubfooter/pubfooter'
import tools from '@/common/tools.js'
import axios from 'axios'

export default {
  name: 'queryexpress',
  data: function() {
    return {
      nowOrderId: '',
      allData: []
    }
  },
  created() {
    this.nowOrderId = this.$route.query.orderId
    this._getExpressInfo()
  },
  watch: {
    '$route' (to, from) { // 监听路由是否变化
      if (to.name === 'queryexpress') {
        console.log('路由变化了', this.$route.query.orderId)
        this.nowOrderId = this.$route.query.orderId
        this._getExpressInfo()
      }
    }
  },
  methods: {
    _getExpressInfo() {
      let url = '/Mobile/Order/query_express.html'
      axios.get(url, {
        params: {
          order_id: this.nowOrderId
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            this.allData = res.data.data
          } else {
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  components: {
    pubfooter
  }
}



